@media (min-width: 660px) {
  .banner {
    position: relative;
    background: url("../img/background-banner-tablet.jpg") no-repeat;
    min-height: 729px;
    padding: 0 calc(50% - 310px);
    padding-top: 210px;
    padding-bottom: 40px;
    box-sizing: border-box;
    background-size: cover;
    background-position: 100% center;
  }
  @supports (background: url("../img/background-banner-tablet.webp")) {
    .banner {
      background: url("../img/background-banner-tablet.webp") no-repeat;
      background-size: cover;
    }
  }
  .banner::before {
    content: "";
    background: url("../img/phone-image-banner-tablet.png") no-repeat;
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 660px;
    height: 545px;
    background-size: 660px auto;
    left: 50%;
    transform: translateX(-50%);
  }
  @supports (background: url("../img/phone-image-banner-tablet.webp")) {
    .banner::before {
      background: url("../img/phone-image-banner-tablet.webp") no-repeat;
      background-size: cover;
    }
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .banner {
    background: url("../img/background-banner-tablet@2x.jpg") no-repeat;
    background-size: cover;
  }
  @supports (background: url("../img/background-banner-tablet@2x.webp")) {
    .banner {
      background: url("../img/background-banner-tablet@2x.webp") no-repeat;
      background-size: cover;
    }
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .banner::before {
    background: url("../img/phone-image-banner-tablet@2x.jpg") no-repeat;
    background-size: cover;
  }
  @supports (background: url("../img/phone-image-banner-tablet@2x.webp")) {
    .banner::before {
      background: url("../img/phone-image-banner-tablet@2x.webp") no-repeat;
      background-size: cover;
    }
  }
