  content: attr(data-hit-text);
  display: block;
  position: absolute;
  top: 21px;
  left: 14px;
  transform: rotate(-45deg);
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
}
.table-head__tariff-title {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
}
.table-head__tariff-price {
  display: block;
  font-size: 18px;
  font-weight: 400;
  color: #ffffff;
}
.table-body__cell {
  padding: 19px 17px 19px 20px;
  background-color: #ffffff;
}
.table-body__filter-title {
  display: none;
}
.table-body__feature {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.table-body__feature--pink::before {
  content: attr(data-feature-name);
}
.table-body__feature--emoticons::before {
  content: attr(data-feature-name);
}
.table-body__feature--comments::before {
  content: attr(data-feature-name);
}
.table-body__availability--yes::before {
  content: url("../img/icons/yes.svg");
  display: block;
  width: 31px;
  height: 21px;
}
.table-body__availability--no::before {
  content: url("../img/icons/no.svg");
  display: block;
  width: 27px;
  height: 27px;
}
.contacts {
  padding-top: 48px;
}
.contacts__title {
  margin-bottom: 45px;
}
.contacts__map {
  margin-top: 41px;
}
.contacts-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  row-gap: 33px;
}
.contacts-list__title {
  font-size: 18px;
  font-weight: bold;
  margin: 0;
  margin-bottom: 13px;
}
.contacts-list__link {
  font-family: "Open Sans Light";
  font-size: 24px;
  color: #d22856;
  text-decoration: none;
  font-weight: 400;
  word-wrap: break-word;
}
.map {
  position: relative;
}
.map::before {
  content: " ";
  height: 33px;
  width: 100%;
  position: absolute;
  top: -1px;
  left: 0;
  -webkit-clip-path: polygon(0 100%, 50% 100%, 0 0);
  clip-path: polygon(100% 0, 50% 100%, 0 0);
  background-color: #ffffff;
  z-index: 100;
}
.map__image {
  display: block;
  width: 100%;
  max-height: 200px;
  object-fit: cover;
}
.map__iframe {
  border: 0;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.main-footer {
  padding: 40px 0;
}
.main-footer__container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 38px;
  justify-items: center;
}
.main-footer__social-list {
  width: 180px;
}
.main-footer__copyright {
  width: 130px;
}
.social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.social-list__link {
  display: flex;
  position: relative;
  width: 54px;
  height: 54px;
  border: 3px solid #f2f2f2;
  box-sizing: border-box;
  border-radius: 50%;
}
.social-list__link--vk::before {
  content: url("../img/icons/vk.svg");
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
}
.social-list__link--telegram::before {
  content: url("../img/icons/telegram.svg");
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
}
.social-list__link--youtube::before {
  content: url("../img/icons/youtube.svg");
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
}
.copyright {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  color: #1D2631;
}
.copyright__text {
  font-size: 14px;
  line-height: 18px;
}
.effect {
  padding-top: 55px;
}
@media (max-width: 659px) {
  .effect--none {
    display: none;
  }
}
.effect__container {
  position: relative;
  display: grid;
  grid-template-columns: 268px 1fr;
  grid-template-rows: auto minmax(80px, auto) auto;
  grid-template-areas: "img title" "img text" "img link";
  column-gap: 41px;
  row-gap: 11px;
}
.effect__container::before {
  content: url("../img/icons/effect-time.svg");
  display: block;
  position: relative;
  width: 238px;
  height: 276px;
  grid-area: img;
  top: 50%;
  transform: translateY(-50%);
}
.effect__title {
  grid-area: title;
  font-family: "Open Sans Bold";
  margin: 0;
  width: calc(100% - 139px);
  font-size: 30px;
  line-height: 36px;
  align-self: end;
}
.effect__text {
  grid-area: text;
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
}
.effect__link {
  color: #d22856;
  font-size: 14px;
  line-height: 24px;
  grid-area: link;
}
